@import "https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  color: #4d4751;
  background-color: #efeef1;
  font-family: Poppins, arial, helvetica, sans-serif;
  font-size: 14px;
  line-height: 1.6;
}

li {
  list-style: none;
}

.breed-select {
  height: 30px;
  width: 200px;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid gray;
  border-radius: 5px;
  margin-bottom: 50px;
}

.cat-info-wrapper {
  min-height: 300px;
  width: 1000px;
  max-width: 90%;
  margin: 0 auto;
  display: flex;
  position: relative;
}

.cat-info {
  text-align: left;
  height: inherit;
  width: inherit;
  background-color: #fff;
  border-radius: 10px;
  align-items: center;
  gap: 20px;
  padding: 40px 30px;
  display: flex;
}

.cat-info > img {
  width: 300px;
  height: 300px;
  object-fit: cover;
  border-radius: 20px;
  display: block;
}

.temperament {
  margin-top: 20px;
  margin-bottom: 20px;
  font-weight: 700;
}

.temperament > span {
  font-weight: 400;
}

.loader {
  width: 100%;
  height: 100%;
  background-color: #f7f9fb;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.hidden {
  opacity: 0;
  visibility: hidden;
}

.loader:after {
  content: "";
  width: 75px;
  height: 75px;
  border: 15px solid #ddd;
  border-top-color: #7449f5;
  border-radius: 50%;
  animation: loading .75s infinite;
}

@keyframes loading {
  from {
    transform: rotate(0turn);
  }

  to {
    transform: rotate(1turn);
  }
}

.progress-list {
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  display: grid;
}

.progress-list p {
  text-transform: uppercase;
  font-weight: 700;
  display: inline-grid;
}

.ProgressBar-module--bar--2c5bb {
  border-radius: 20px;
  margin-top: 10px;
}

